.mark-list {
  line-height: 42px;
  margin-bottom: -28px;
  &-item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 28px;
    &:before {
      content: "";
      background-color: #891818;
      height: 22px;
      width: 22px;
      border-radius: 50%;
      margin-right: 8px;
    }
  }
  
}