/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

*, *::before, *::after {
  box-sizing: border-box; }

body {
  font-family: 'Roboto', sans-serif;
  text-align: center;
  font-weight: 500;
  font-size: 24px; }

h2 {
  font-size: 64px;
  line-height: 84px;
  font-weight: 500; }

h3 {
  line-height: 42px; }

p {
  font-size: 20px;
  line-height: 1.5; }

a {
  color: inherit;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }
  a:active {
    text-decoration: none; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

.mark-list {
  line-height: 42px;
  margin-bottom: -28px; }
  .mark-list-item {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 28px; }
    .mark-list-item:before {
      content: "";
      background-color: #891818;
      height: 22px;
      width: 22px;
      border-radius: 50%;
      margin-right: 8px; }

.dark {
  background-color: #000;
  color: #fff; }

.text-bold {
  font-weight: 700; }

.lh-m {
  line-height: 1.75; }

.d-flex {
  display: flex; }

.flex-col {
  flex-direction: column; }

.flex-wrap {
  flex-wrap: wrap; }

.justify-content-center {
  justify-content: center; }

.justify-content-between {
  justify-content: space-between; }

.align-items-center {
  align-items: center; }

.d-inline-block {
  display: inline-block; }

.align-left {
  text-align: left; }

.mx-auto {
  margin-left: auto;
  margin-right: auto; }

.mx-2 {
  margin: 0 8px; }

.mb-1 {
  margin-bottom: 4px; }

.mb-2 {
  margin-bottom: 8px; }

.mb-3 {
  margin-bottom: 12px; }

.mb-4 {
  margin-bottom: 16px; }

.mb-5 {
  margin-bottom: 20px; }

.mb-8 {
  margin-bottom: 32px; }

.mb-10 {
  margin-bottom: 40px; }

.mb-12 {
  margin-bottom: 48px; }

.mb-14 {
  margin-bottom: 56px; }

.mb-17 {
  margin-bottom: 86px; }

.py-14 {
  padding: 56px 0; }

.pt-14 {
  padding-top: 56px; }

.px-16 {
  padding: 64px 0; }

.pt-36 {
  padding-bottom: 144px; }

.pb-40 {
  padding-bottom: 160px; }

.light-on-link {
  display: block;
  position: relative; }
  .light-on-link::before {
    border-radius: 25%;
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.3;
    transition: all 0.1s; }
  .light-on-link:hover::before {
    background-color: #fff; }

.banner {
